@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;500;600;700&family=Source+Sans+Pro:wght@400;600;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  width: 100%;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.05em;
  height: 100%;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Lora', serif;
}

a {
  color: #ff0033;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:active, a:focus {
  background: rgba(0, 0, 0, .15);
}

input, button, select {
  font-family: 'Source Sans Pro', sans-serif;
}

.homescreensplash {
  animation: splashing 3s infinite;
}

.button-hamburger.homescreensplash-2 .hamburger {
  animation: hamburger-splashing 3s infinite;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 34px 0 100px 0;
}

.about-footer {
  text-align: right;
  font-size: 0.8em;
  color: #333333;
  opacity: 0.5;
  padding: 0 25px;
}

.container-inner {
  width: 65%;
  padding: 0 10px;
}

.main-content {
  margin-top: 30px;
}

.absolute-page {
  overflow: hidden;
  box-shadow: 0 10px 20px 10px rgba(0, 0, 0, .05);
  margin: 25px 0;
  background: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 8px;
}

.absolute-content {
  background: #f5f5f5;
  padding: 25px;
  width: 400px;
}

.absolute-content form .calculator-title {
  position: relative;
  top: -8px;
}

/* Misc. */
.flex {
  display: flex;
}

.relative {
  position: relative;
}

.flex.start {
  align-items: flex-start !important;
}

.form-control.flex {
  align-items: center;
  justify-content: space-between;
}

.form-control.flex.no-space {
  align-items: center;
  justify-content: unset !important;
}

.form-control.inner.flex {
  align-items: flex-start;
  justify-content: unset;
}

.form-control.inner:nth-child(3) {
  margin-top: 20px;
}

#destInput:nth-child(1n) {
  margin-top: 20px;
  box-shadow: -22px 0 0 #f5f5f5, -30px 0 0 #ff0033;
}

#destInput:last-child {
  margin-bottom: 20px;
}

.form-control {
  gap: 15px;
}

.hide {
  display: none;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.visible {
  visibility: visible;
  opacity: 1;
}

.block {
  display: block;
}

.disabled {
  transition: 0.14s;
  opacity: 0.3;
  pointer-events: none;
}

.expanded {
  justify-content: space-between;
}

.gap-1 {
  gap: 10px;
}

.gap-2 {
  gap: 20px;
}

.gap-3 {
  gap: 30px;
}

.flex-between {
  justify-content: space-between;
}

.inline {
  display: inline;
}

.no-padding {
  padding: 0 0 !important;
}

.align-right *{
  text-align: right;
}

.m-only {
  display: none;
}

/* Homescreen */
.homescreen-arrow {
  font-size: 2em;
  padding: 0 60px;
  position: relative;
  animation: floats 2s infinite;
}

.homescreen-content {
  padding: 30px;
}

/* About */
.timeline-item {
  margin: 20px 40px;
  line-height: 1.5;
}

/* Password prompt */
.password-prompt ._37_FJ {
  border: 2px solid #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
}

.password-prompt ._37_FJ ._ovIFV {
  display: none;
}

.password-prompt ._37_FJ div:nth-child(2):before {
  content: "Masukkan password " '\A' "untuk menggunakan aplikasi";
  white-space: pre-line;
  background: #ff0033;
  border-radius: 4px;
  display: block;
  color: #ffffff;
  padding: 10px;
  margin: 10px 0;
  font-weight: bold;
  min-width: 300px;
}

.password-prompt ._37_FJ div:nth-child(2) input {
  padding: 10px 15px;
  width: 100%;
  font-size: .95em;
  border: 1px solid #888888;
  border-radius: 4px;
  outline: none;
  transition: .15s;
}

.password-prompt ._37_FJ div:nth-child(2) input:hover {
  border: 1px solid #333333;
}
.password-prompt ._37_FJ div:nth-child(2) input:focus {
  background: #ffffff;
  box-shadow: 0 0 0 3px #ff003327;
  border: 1px solid #ff0033;
  transition: 0s;
}

.password-prompt ._1YwH3 {
  align-self: start;
}
.password-prompt ._1YwH3 button {
  padding: 10px 15px;
  margin-top: -10px;
  float: left;
  font-size: .9em;
}

@keyframes splashing {
  0% {
    box-shadow: 0 0 0 3px transparent;
  }
  50% {
    box-shadow: 0 0 0 3px #ff0033;
    border-radius: 4px;
  }
  100% {
    box-shadow: 0 0 0 3px transparent;
  }
}

@keyframes hamburger-splashing {
  0% {
    background: #888888;
  }
  50% {
    background: #ff0033;
  }
  100% {
    background: #888888;
  }
}

@keyframes floats {
  0% {
    top: -10px;
  }
  50% {
    top: 10px;
  }
  100% {
    top: -10px;
  }
}


@media screen and (max-width: 1023px) {
  .m-only {
    display: block;
  }

  .m-no-flex {
    display: unset;
  }

  .m-display-none {
    display: none;
  }

  .app-header.m-no-flex {
    padding: unset;
    width: 90%;
  }

  .app-header.m-no-flex h2 {
    padding: 0 10px;
  }

  .app-header.m-no-flex .button-link-dropdown {
    border: 1px solid transparent;
    border-top: 1px solid #888888;
    margin: 10px 0;
    padding: 10px;
    border-radius: 0;
  }

  .dropdown-container.m-sidebar {
    position: fixed;
    left: 0;
    top: 0;
    width: 70%;
    max-width: 300px;
    height: 100%;
    margin: 0;
  }

  .m-sidebar-overlay {
    position: fixed;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .15);
    top: 0;
    left: 0;
    z-index: 7;
  }

  .container-inner {
    padding: unset;
    width: 90%;
  }

  .container-inner .absolute-page.m-no-flex {
    display: block;
  }

  .container-inner .absolute-page.m-no-flex .absolute-content {
    width: unset;
  }

  .container-inner .absolute-page.m-no-flex .result-list-container {
    box-shadow: unset;
  }

  .revealealonhover {
    visibility: visible !important;
  }

  .switch-view.m-block.m-text-center {
    display: block;
    margin: 0 auto;
    width: 100%;
    text-align: center;
  }

  .option.m-inline-flex {
    display: inline-flex;
  }

  .m-flex-justify-end {
    justify-content: end;
  }

  .input-container.m-shrink .inner .input-text {
    width: 30vw;
  }

  .m-align-unset * {
    text-align: unset;
  }

  /* Hamburger menu */
  .m-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    top: -12px;
    background: #f5f5f5;
    gap: 5px;
    border-radius: 4px;
    overflow: hidden;
  }
  
  .button-hamburger {
    width: 3rem;
    height: 3rem;
    display: inline-block;
    position: relative;
    padding: 11px 13px;
    border: 1px solid transparent;
    box-shadow: inset -8px 0 0 8px #f5f5f5, inset 1px 0 0 #cccccc;
    cursor: pointer;
    margin: 0 0 -5px 0;
  }
  
  .button-hamburger .hamburger-container {
    transition: .3s;
  }
  
  .button-hamburger .hamburger {
    margin: 5px 0;
    transition: .3s;
    position: relative;
    top: 0;
    left: 0;
  }
  
  .button-hamburger .hamburger.first {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger .hamburger.second {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger .hamburger.third {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger.focus .hamburger-container {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
  }
  
  .button-hamburger.focus .hamburger.first {
    -webkit-transform: rotate(40deg);
    transform: rotate(40deg);
    width: 70%;
    top: 3px;
    left: 10px
  }
  
  .button-hamburger.focus .hamburger.third {
    -webkit-transform: rotate(-40deg);
    transform: rotate(-40deg); 
    width: 70%;
    top: -3px;
    left: 10px;
  }
  
  .button-hamburger:hover .hamburger {
    background: #333333;
  }
  
  .button-hamburger:active {
    background: #eeeeee;
    box-shadow: inset -8px 0 0 8px #eeeeee, inset 1px 0 0 #cccccc;
  }  
}