.option-container.flex {
  align-items: center;
}

.option-container .inner {
  height: 40px;
  background: #ffffff;
  border: 1px solid #888888;
  padding: 10px;
  font-size: 0.9em;
  border-radius: 3px;
  outline: none;
  margin: 2px 0;
  transition: 0.15s;
  min-width: 200px;
  
  /* Stylig */
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  padding: 0 25px 0 10px;
}

.dropdown-icon {
  display: inline-block;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 7px solid #555555;
  z-index: 1;
  margin-left: -25px;
  margin-right: 10px;
}

.option-container .inner:hover {
  border: 1px solid #333333;
}
.option-container .inner:focus {
  border: 1px solid #ff0033;
  box-shadow: 0 0 0 3px #ff003327;
}