.button {
  padding: 10px;
  background: #ffffff;
  border: 1px solid #888888;
  border-radius: 3px;
  cursor: pointer;
  min-width: 80px;
  font-size: 0.95em;
  transition: 0.15s;
  margin: 5px 0;
}

.button-link {
  cursor: pointer;
  padding: 0 5px;
}

.button-link-dropdown {
  background: #ffffff;
  padding: 5px 5px 5px 10px;
  border: 1px solid #888888;
  border-radius: 4px;
  color: #333333;
  cursor: pointer;
  transition: 0.15s;
}

.button-link-dropdown .dropdown-icon {
  margin-left: 10px;
  margin-top: 8px;
  margin-right: 7px;
  border-top: 7px solid #888888;
}

.text-primary{
  color: #ff0033;
}

.button.gone {
  visibility: hidden;
}

.button.mini {
  min-width: 55px;
  font-size: .7em;
  padding: 0 10px;
}

.revealonhover {
  transition: 0s;
  visibility: hidden;
}

.primary {
  background: #ff0033;
  border: 1px solid transparent;
  color: #ffffff;
}

.button:focus, .button-link-dropdown:focus {
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.35), 0 0 0 3px #ff003327;
}

.button:hover {
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.15);
}

.button:active {
  box-shadow: 0 0 0 3px #ff003327;
  transition: 0s;
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.35);
}

.button:focus {
  text-decoration: underline;
}

.button-link:hover {
  text-decoration: underline;
}

.button-link:active {
  background: rgba(0, 0, 0, .1);
  text-decoration: none;
}

.button-link-dropdown:hover {
  border: 1px solid #333333;
}

.button-link-dropdown:hover .dropdown-icon {
  border-top: 7px solid #555555;
}

.button-link-dropdown:active {
  background: #f5f5f5;
}


/* Dropdown */
.button-link-dropdown.custom {
  padding: 5px 15px 5px 25px;
  border: 1px solid transparent;
  border-radius: 0;
  border-left: 1px solid #888888;
}

.button-link-dropdown:hover {
  border: 1px solid #333333;
  border-radius: 4px;
}

.button-link-dropdown.custom.focus {
  background: #f5f5f5;
  border-radius: 4px;
  border: 1px solid #333;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .15);
}

.dropdown-container {
  position: absolute;
  background: #f5f5f5;
  border-radius: 4px;
  padding: 15px 0;
  border: 2px solid #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
  margin-top: 7px;
  z-index: 9;
  transition: .15s;
  max-height: 400px;
  overflow-y: auto;
}

.dropdown-container.hidden {
  opacity: 0;
}

.dropdown-container.visible.animated {
  animation: slide-fade .15s ;
}

@media screen and (max-width: 1023px) {
  .dropdown-container.m-sidebar.visible.animated {
    animation: slide-to-right .3s cubic-bezier(.13,.05,0,1.01);
  }
  .dropdown-container {
    max-height: unset;
  }
}

.dropdown-container * {
  display: block;
  padding: 15px 15px;
  margin: 0 15px;
  text-decoration: none;
  color: #000000;
}

.dropdown-container *:nth-of-type(even) {
  border-bottom: 1px solid #e0e0e0;
  border-top: 1px solid #e0e0e0;
}

.dropdown-container *:hover {
  background: #e5e5e5;
}

.dropdown-container *:active {
  background: #e5e5e5;
}

@keyframes slide-fade {
  0% {
    margin-top: 0;
    opacity: 0;
  }
  100% {
    margin-top: 7px;
    opacity: 1;
  }
}

@keyframes slide-to-right {
  0% {
    transform: translateX(-120%);
    -webkit-transform: translateX(-120%);
  }
  100% {
    transform: translateX(0%);
    -webkit-transform: translateX(0%);
  }
}