.switch-view {
  align-items: center;
  gap: 10px;
  margin: 0 0 15px 0;
  padding: 10px 10px 20px 10px;
  border-bottom: 1px solid #efefef;
  justify-content: end;
  font-size: .8em;
}

.switch-view .option {
  gap: 0;
  border-radius: 100px;
  border: 1px solid #555555;
  overflow: hidden;
}

.tabswitcher-container {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 20px;
  text-transform: capitalize;
}

.tabswitcher-container input {
  position: absolute;
  opacity: 0;
}

.tabswitcher-container label {
  position: relative;
  z-index: 1;
  cursor: pointer;
}

.tabswitcher-container .marker {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: 0.15s;
}

.tabswitcher-container input:hover ~ .marker {
  background: #cccccc;
}

.tabswitcher-container input:focus ~ label {
  text-decoration: underline;
}

.tabswitcher-container input:active ~ .marker {
  background: #aaaaaa;
}

.tabswitcher-container input:checked ~ .marker {
  background: #555555;
}

.tabswitcher-container input:checked ~ label {
  color: #eeeeee;
}
