.result-list-container {
    flex: 1;
    background: #ffffff;
    padding: 15px 20px;
    box-shadow: -10px 0 20px rgba(0, 0, 0, .05);
    transition: .15s;
    line-height: 2;
}

.result-list-inner.flex {
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}

.result-list-inner {
    padding: 0 10px;
}

.result-list-inner:hover {
    box-shadow: inset 0 0 0 100px rgba(0, 0, 0, .05);
}

.result-list-inner:nth-of-type(odd) {
    background: #ffeaea;
}

.result-list-inner:last-child {
    background: #ff0033;
    color: #ffffff;
    font-weight: bold;
}

.result-list-inner .list-content .price-content {
    padding: 0 0 0 10px;
    text-align: right;
}

.result-list-inner:hover .list-content .revealonhover {
    visibility: visible;
}

.after-discount {
    margin-top: 30px;
    padding: 20px 0 0 0;
    border-top: 1px solid #cccccc;
}

.submode-metro {
    background: #ff0033 !important;
    color: #ffffff;
    font-weight: bold;
}

