  }

  /* Hamburger menu */
  .m-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    top: -12px;
    background: #f5f5f5;
    grid-gap: 5px;
    gap: 5px;
    border-radius: 4px;
    overflow: hidden;
  }
  
  .button-hamburger {
    width: 3rem;
    height: 3rem;
    display: inline-block;
    position: relative;
    padding: 11px 13px;
    border: 1px solid transparent;
    box-shadow: inset -8px 0 0 8px #f5f5f5, inset 1px 0 0 #cccccc;
    cursor: pointer;
    margin: 0 0 -5px 0;
  }
  
  .button-hamburger .hamburger-container {
    transition: .3s;
  }
  
  .button-hamburger .hamburger {
    margin: 5px 0;
    transition: .3s;
    position: relative;
    top: 0;
    left: 0;
  }
  
  .button-hamburger .hamburger.first {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger .hamburger.second {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger .hamburger.third {
    width: 100%;
    height: 2px;
    background: #888888;
  }
  
  .button-hamburger.focus .hamburger-container {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
  }
  
  .button-hamburger.focus .hamburger.first {
    -webkit-transform: rotate(40deg);
    transform: rotate(40deg);
    width: 70%;
    top: 3px;
    left: 10px
  }
  
  .button-hamburger.focus .hamburger.third {
    -webkit-transform: rotate(-40deg);
    transform: rotate(-40deg); 
    width: 70%;
    top: -3px;
    left: 10px;
  }
  
  .button-hamburger:hover .hamburger {
    background: #333333;
  }
  
  .button-hamburger:active {
    background: #eeeeee;
    box-shadow: inset -8px 0 0 8px #eeeeee, inset 1px 0 0 #cccccc;
  }  
}
