.skeleton-loading {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.skeleton-form {
  width: auto;
}

.skeleton-form * {
  display: block;
  margin: 10px 0;
}

.skeleton-title {
  width: 150px;
  height: 23px;
  background: #e0e0e0;
  position: relative;
  overflow: hidden
}

.skeleton-label {
  width: 150px;
  height: 28px;
  background: #e0e0e0;
  position: relative;
  overflow: hidden
}

.skeleton-input {
  width: auto;
  height: 40px;
  background: #e0e0e0;
  position: relative;
  overflow: hidden;
}

.skeleton-button {
  width: 80px;
  height: 40px;
  background: #ffcdcd;
  position: relative;
  overflow: hidden;
}

.skeleton-form *:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  /* background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(rgba(255, 255, 255, 0.2)), to(transparent)); */
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.78), transparent);
  -webkit-animation: loading 1s infinite;
          animation: loading 1s infinite;
}

@-webkit-keyframes loading {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes loading {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}
