.radiogroup-container {
  position: relative;
}

.radiobutton-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 5px 0;
}

.radiobutton-container input {
  position: absolute;
  opacity: 0;
}

.radiobutton-container label {
  position: relative;
  z-index: 1;
  cursor: pointer;
  margin-left: 25px
}

.radiobutton-container label:before {
  content: "";
  position: absolute;
  left: -25px;
  top: 2px;
  width: 15px;
  height: 15px;
  border: 2px solid #888888;
  border-radius: 100%;
  transition: 0.15s;
  overflow: hidden;
}

.radiobutton-container input ~ label:after {
  content: "";
  position: absolute;
  width: 9px;
  height: 9px;
  top: 7px;
  left: -20px;
  background: #ff0033;
  border-radius: 100%;
  transform: scale(0);
  -webkit-transform: scale(0);
  transition: .3s cubic-bezier(0,.51,0,1.7);
}

.radiobutton-container input:hover ~ label:before {
  border: 2px solid #333333;
}

.radiobutton-container input:focus~label {
  text-decoration: underline;
}

.radiobutton-container input:active ~ label:before {
  background: #cccccc;
}

.radiobutton-container input:checked ~ label:before {
  border: 2px solid #ff0033;
}

.radiobutton-container input:checked ~ label:after {
  transform: scale(1);
  -webkit-transform: scale(1);
}
