.form-control .input-container {
  margin-top: 0 !important;
}

/* .input-container {
  margin-top: 20px;
} */

.form-control .section {
  margin: 6px 0;
}

.input-container.mini .inner {
  height: 30px;
}

.input-container .inner {
  gap: 10px;
  margin: 2px 0;
  background: #ffffff;
  border: 1px solid #888888;
  padding: 0 10px;
  height: 40px;
  border-radius: 3px;
  transition: 0.15s;
  align-items: center;
}

.input-container .inner .input-text {
  background: transparent;
  border: 1px solid transparent;
  font-size: 0.95em;
  flex: 1;
  outline: none;
  height: 100%;
}

.input-readonly {
  background: #ffffff;
  border: 1px solid transparent;
  padding: 0 10px;
  font-size: 0.95em;
  text-align: right;
}

.input-readonly:focus {
  border: 1px solid transparent;
  outline: none;
}

.input-container .inner:hover {
  border: 1px solid #333333;
}

.input-container .inner:focus-within {
  background: #ffffff;
  box-shadow: 0 0 0 3px #ff003327;
  border: 1px solid #ff0033;
  transition: 0s;
}

.input-container .inner .label {
  color: #333333;
}
