  margin-top: 12px;
  margin-left: 10px;
}

.input-container .inner.flex .label {
  margin-top: 12px;
  margin-left: 0 !important;
} */

.input-container .helper, .form-control .helper, .helper {
  display: block;
  font-size: 0.7em;
  color: #555555;
  margin: 0 0;
}

.input-container .helper.error, .form-control .helper.error, .helper.error {
  color: #ff0033;
}

.helper.op5 {
  margin-top: 0;
  color: #555555;
}

.helper.big {
  display: block;
  padding: 5px 10px;
  border-left: 2px solid #000000;
  margin: 10px 0;
  font-size: unset;
  color: #000000;
}

.big.error {
  background: #ff003313;
  border-left: 2px solid #ff0033;
}

.big.warning {
  background: #ffe60013;
  border-left: 2px solid #ffe600;
}